import request from './request'
import cache from './indexeddbCache'

async function updatePscData(cacheKey, cacheData) {
    // https://pscdata.gotopanda.ml/
    // http://10.192.1.141:6006/
    if (cacheData) {
        const uua = await request.get(
            "updatePscData", {
                params: {
                    key: cacheKey,
                    start: cacheData.value["data"].length
                }
            }
        )
        if ((uua.data["data"].length === 0)) {
            return false
        } else {
            cacheData.value["data"].push.apply(cacheData.value["data"], uua.data["data"])
            cacheData.value["endBlock"] = uua.data["endBlock"]
            await cache.addCache(cacheData)
            return true
        }
    } else {
        const uua = await request.get(
            "updatePscData", {
                params: {
                    key: cacheKey
                }
            }
        )
        const data = {
            key: cacheKey,
            value: uua.data
        }
        await cache.addCache(data)
        return true
    }
}

export {
    updatePscData
}