function searchDateRange(arry, dateStart, dateEnd) {
    // console.log(arry);
    // 
    // 
    const res = arry.filter(e => ((new Date(e.date).getTime() > dateStart.getTime()) && (new Date(e.date).getTime() < dateEnd.getTime())))
    return res
}

function searchByuserAddressFilter(arry, filter) {
    // console.log(arry);
    const res = arry.filter(e => (e.userAddress.includes(filter)))
    return res
}

export {
    searchByuserAddressFilter,
    searchDateRange
}